import React from "react";
import { useParams } from "react-router-dom";

import { ReactMarkdown } from "react-markdown/lib/react-markdown";

import { Prism as SyntaxHighlighter } from "react-syntax-highlighter";
import light from "react-syntax-highlighter/dist/esm/styles/prism/vs";
import dark from "react-syntax-highlighter/dist/esm/styles/prism/vsc-dark-plus";

import PageNotFound from "./404";

export default function Post({isDarkMode}){
    const [details, setDetails] = React.useState(null);
    const [content, setContent] = React.useState("");
    const { url } = useParams();
    

    React.useEffect(() => {
        (async () => {
            const lib = await (await fetch("/library.json")).json();
            const post = await (await fetch(`/posts/${url}.md`)).text();
    
            Promise.all([lib, post])
            .then(data => {
                const detail = data[0].posts.filter(post => post.url.match(url))[0];

                if(!detail) return setContent(null);

                setDetails(detail);
                setContent(data[1]);
    
                document.title = detail.name + " | @velickovicdj";
            });
        })();
    }, []);

    return(<>
        {content === null ? <PageNotFound />
        :
        <div className="container">
            <div className="wrapper">
                {details === null && <div className="load-ring" />}
                <div className="markdown-container normal">
                    {details &&
                    <div className="post">
                        <label className="post-count">{details.date}</label>
                        <h1>{details.name}</h1>
                        <p>Posted in <a href="/blog">{details.tag}</a></p>
                    </div>
                    }   
                    <ReactMarkdown
                        className="markdown"
                        children={content}
                        components={{
                            code({node, inline, className, children, ...props}){
                                const match = /language-(\w+)/.exec(className || '')
                                return !inline && match ? (
                                <SyntaxHighlighter
                                    children={String(children).replace(/\n$/, '')}
                                    language={match[1]}
                                    style={isDarkMode ? dark : light}
                                    PreTag="div"
                                    {...props}
                                />
                                ) : (
                                <code className={className} {...props}>
                                    {children}
                                </code>
                                )
                            }
                        }}
                    />
                    {details !== null && 
                    <div className="sharing">
                        <h2>Share on:</h2>
                        <div className="share-buttons">
                            <a href="#">
                                <h3><i className="fa fa-twitter" /></h3>
                            </a>
                            <a href="#">
                                <h3><i className="fa fa-reddit" /></h3>
                            </a>
                        </div>
                    </div>
                    }
                </div>
            </div>
        </div>
        }
    </>);
}