import React from "react";

export default function Search({library}){
    const [string, setString] = React.useState("");
    const [result, setResult] = React.useState([]);
    const [selected, setSelected] = React.useState(0);

    const handleSearch = e => {
        setString(e.target.value);
        
        const search = e.target.value.trim().toLowerCase();

        if(search.length === 0) return setResult([]);

        setResult(library.posts.filter(post => post.name.toLowerCase().match(search)));
    }
    
    const handleEnter = e => {        
        switch(e.keyCode){
            case 13:
                return window.location.href = "/blog/" + result[selected].url;
            
            case 38:
                e.preventDefault();
                if(selected>0) return setSelected(selected-1);

                break;

            case 40:
                e.preventDefault();
                if(selected<result.length-1) return setSelected(selected+1);

                break;
                
            default:
                break;
        }
    }

    return(
        <div className="search-box">
            <input 
                type="text"
                value={string} 
                onChange={handleSearch} 
                onKeyDown={handleEnter}
                placeholder="Search" 
            />
            <ul className="search-results">
                {result.map((post, key) => (
                    <a 
                        key={key}
                        className={selected === key ? "result selected" : "result"}
                        href={"/blog/"+post.url}
                    >
                        <label name="post-count">{post.tag}</label>
                        <li><h3>{post.name}</h3></li>
                    </a>
                ))}
            </ul>
        </div>
    );
}