import * as React from "react";
import { Helmet } from "react-helmet";

import NavBar from "./components/NavBar";
import Reader from "./components/Reader";
import Viewer from "./components/Viewer";

import "./assets/scss/index.scss";

export default function OpenCoreSanityChecker(){
	document.body.classList.add("opencore");

	const [viewerData, setViewerData] = React.useState({});
	
	return(<>
		<Helmet>
			<title>OpenCore Sanity Checker</title>
		</Helmet>
		<NavBar />
		{viewerData.file ?
			<Viewer viewerData={viewerData} />
		:
			<Reader 
				setViewerData={setViewerData} 
			/>
		}
	</>);
}