import React from "react";

export default function ModeToggle({isDarkMode, setDarkMode}){

    const switchMode = () => {
        if(isDarkMode){
            setDarkMode(false);
            document.body.classList.remove("dark");
            localStorage.removeItem("dark");
        }else{
            setDarkMode(true);
            document.body.classList.add("dark");
            localStorage.setItem("dark", true);
        }
    }

    return(
        <div className={isDarkMode ? "mode-toggle mode-toggle-dark" : "mode-toggle"} onClick={switchMode}>
            <div className="path">
                <div className={isDarkMode ? "circle circle-dark" : "circle"}></div>
            </div>
        </div>
    );
}