import React from "react";
import { Helmet } from "react-helmet";

import Search from "../components/Search";

export default function Blog(){
    const [open, setOpen] = React.useState([]);
    const [library, setLibrary] = React.useState({});

    React.useEffect(() => {
        fetch("/library.json")
            .then(res => res.json())
            .then(res => {
                let arr = [];
                
                res.tags.forEach(() => arr.push(false));

                setOpen(arr);
                setLibrary(res);
        });
    }, []);

    const getPostByTag = tag => library.posts.filter(post => post.tag.match(tag));

    const toggleOpen = (e, postIndex) => {
        e.preventDefault();

        const updatedOpen = open.map((el, index) => index === postIndex ? !el : el);
        
        setOpen(updatedOpen);
    }

    return(<>
        <Helmet>
            <title>Blog | @velickovicdj</title>
        </Helmet>
        <div className="container">
            <div className="wrapper">
                <div className="section-container normal">
                    <div className="section">
                        <div className="section-title">
                            <h1>Browse the archive</h1>
                            <p>Posts from the past 2 years sorted in categories.</p>
                        </div>
                        <div className="section-content">
                            <Search
                                library={library}
                            />
                            {library?.tags?.map((tag, key) => (
                            <div key={key} className="post summary">
                                <div className="post-header" onClick={e => toggleOpen(e, key)}>
                                    <label name="post-count">{getPostByTag(tag).length !== 1 ? `${getPostByTag(tag).length} posts` : "1 post"}</label>
                                    <h2>{tag}</h2>
                                    <span className={open[key] ? "arrow up" : "arrow down"} />
                                </div>
                                <details open={open[key]}>
                                    <summary />
                                    {getPostByTag(tag).map((post, index) => (
                                    <a key={`${key}${index}`} href={"/blog/"+post.url} >
                                        <div className="post">
                                            <label name="post-count">{post.date}</label>
                                            <h3>{post.name}<span className="arrow right" /></h3>
                                        </div>
                                    </a>
                                    ))}
                                </details>
                            </div>
                            ))}
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </>);
}