import React from "react";
import ReactDOM from "react-dom";
import { Routes, Route, BrowserRouter } from "react-router-dom";

import Home from "./views/Home";
import Blog from "./views/Blog";
import Post from "./views/Post";
import CV from "./views/CV";
import About from "./views/About";
import OpenCoreSanityChecker from "./ocsc/index";
import PageNotFound from "./views/404";

import Navbar from "./components/Navbar/Navbar";
import Footer from "./components/Footer";
// import CookieConsent from "./components/CookieConsent";

import "./assets/scss/main.scss";

const App = () => {
    const [isDarkMode, setDarkMode] = React.useState(localStorage.getItem("dark") ?? false);

    const isMain = (path = window.location.pathname.toLowerCase()) => {
        return(path !== "/opencore" && path !== "/dialgo");
    };

    return(<>
        {isMain() && 
        <Navbar
            isDarkMode={isDarkMode}
            setDarkMode={setDarkMode}
        />
        }
        <BrowserRouter>
            <Routes>
                <Route path="/" element={<Home />} />
                <Route path="/blog" element={<Blog />} />
                <Route path="/blog/:url" element={<Post isDarkMode={isDarkMode} />} />
                <Route path="/files/CV_VELICKOVICDJ.pdf" element={<CV />} />
                <Route path="/about" element={<About />} />
                <Route path="/opencore" element={<OpenCoreSanityChecker/>} />
                <Route path="*" element={<PageNotFound />} />
            </Routes>
        </BrowserRouter>
        {isMain() && <Footer />}
        {/* {!localStorage.getItem("cookie_consent") && <CookieConsent />} */}
    </>);
}

ReactDOM.render(<App />, document.getElementById("root"));