import React from "react";
import { Helmet } from "react-helmet";

import ImageView from "../components/ImageView";

export default function Home(){
    const [isImageViewActive, setImageViewActive] = React.useState(false);
    const [viewData, setViewData] = React.useState([]);
    const [posts, setPosts] = React.useState(undefined);
    const [pinned, setPinned] = React.useState(undefined);

    React.useEffect(() => {
        fetch("/library.json")
            .then(res => res.json())
            .then(res => {
                const pinned =  res.posts.find(post => post.pinned);

                setPinned(pinned ?? res.posts.slice(-1).pop());
                setPosts(res.posts.slice(-3).reverse());
            });
    }, []);

    return(<>
        <Helmet>
            <title>@velickovicdj</title>
        </Helmet>
        {/* <ImageView 
            isImageViewActive={isImageViewActive}
            setImageViewActive={setImageViewActive}
            viewData={viewData}
            setViewData={setViewData}
         /> */}
        <div className="container">
            <div className="wrapper">
                <div className="section-container normal">
                    <div className="section-center">
                        <div className="section">
                            <div className="section-title">
                                <h1>Blog Posts</h1>
                            </div>
                            <div className="section-content grid">
                                {typeof pinned !== "undefined" && 
                                <a href={"/blog/"+pinned.url}>
                                    <div className="post pinned">
                                        <label className="post-count">{pinned.pinned ? "📌 Pinned" : "🌱 Latest"} Post</label>
                                        <h2>{pinned.name}<span className="arrow right" /></h2>
                                        <label className="post-count">{pinned.date}</label>
                                    </div>
                                </a>
                                }
                                <div className="row">
                                {typeof posts !== "undefined" && posts.map((post, key) => post.url !== pinned.url && (
                                <a key={key} href={"/blog/" + post.url}>
                                    <div className="post">
                                        <label className="post-count">{post.date}</label>
                                        <h3>{post.name}<span className="arrow right" /></h3>
                                    </div>
                                </a>
                                ))}
                                </div>
                            </div>
                            <a href="/blog">
                                <div className="button"><label>Browse all posts</label></div>
                            </a>
                        </div>
                        <div className="section">
                            <div className="section-title">
                                <h2>Looking for something else?</h2>
                            </div>
                            <div className="section-content">
                                {/* <a href="/dialgo">
                                    <div className="post">
                                        <h3>Dialgo</h3>
                                        <p>Flowchart maker<span className="arrow right" /></p>
                                    </div>
                                </a> */}
                                <a href="/opencore">
                                    <div className="post">
                                        <h3>OpenCore Sanity Checker</h3>
                                        <p>For validating config.plist<span className="arrow right" /></p>
                                    </div>
                                </a>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </>);
}